.topcontrol {
  position: fixed;
  bottom: 5px;
  right: 5px;
  opacity: 1;
  z-index: 100;
  cursor: pointer; }

.topcontrol__link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 49px; }

.topcontrol__link--hidden {
  display: none; }

.topcontrol__arrow {
  position: absolute;
  content: "";
  display: block;
  bottom: -15px;
  width: 28px;
  height: 8px;
  opacity: 0;
  transform: scale3d(0.5, 0.5, 0.5);
  animation: move 3s ease-out infinite; }
  .topcontrol__arrow:first-child {
    animation: move 3s ease-out 1s infinite; }
  .topcontrol__arrow:nth-child(2) {
    animation: move 3s ease-out 2s infinite; }

.topcontrol__arrow::before,
.topcontrol__arrow::after {
  position: absolute;
  content: "";
  display: block;
  top: 0;
  height: 100%;
  width: 51%;
  background: coral; }

.topcontrol__arrow::before {
  left: 0;
  transform: skew(0deg, -30deg); }

.topcontrol__arrow::after {
  right: 0;
  width: 50%;
  transform: skew(0deg, 30deg); }

@keyframes move {
  25% {
    opacity: 1; }
  33% {
    opacity: 1;
    transform: translateY(-30px); }
  67% {
    opacity: 1;
    transform: translateY(-40px); }
  100% {
    opacity: 0;
    transform: translateY(-55px) scale3d(0.5, 0.5, 0.5); } }
